import React from 'react';

function WorkSection({ title, list }) {
  return (
    <ol className='work-section'>
      <li className='work-header'>{title}</li>
      {list.map((item, i) => (
        <li key={i} className='work-body'>
          {item}
        </li>
      ))}
    </ol>
  );
}

export default WorkSection;
