import React from 'react';

function Footer({ links }) {
  return (
    <footer id='footer'>
      <div>Phillip Schulze ©</div>
      <div className='icons'>
        {links.map((link, i) => {
          const [href, src, alt, colorClass] = link;
          return (
            <a key={i} href={href} target='_blank' rel='noopener noreferrer'>
              <img alt={alt} src={src} className={'icon ' + (colorClass ? 'icon-white' : '')}></img>
            </a>
          );
        })}
      </div>
      <div id='year'>{new Date().getFullYear()}</div>
    </footer>
  );
}

export default Footer;
