import React from 'react';

function WorkReferenceSection({ referenceList }) {
  if (referenceList)
    return (
      <ol className='work-section'>
        <li className='work-header'>References</li>
        {referenceList.map(([name, email, cell, comment], i) => (
          <li key={i} className='work-ref'>
            <span className='ref-name'>
              {name}
              {comment && <span className='comment'>{comment}</span>}
            </span>
            {cell && <span className='ref-cell'>{cell}</span>}
            {email && <span className='ref-email'>{email}</span>}
          </li>
        ))}
      </ol>
    );
  else return '';
}

export default WorkReferenceSection;
