import React from 'react';

function AcademicSection({ title, largeList, smallList, valueList, comments }) {
  return (
    <ol>
      <li className='ol-header '>
        <h3>{title}</h3>
      </li>
      {largeList.map((large, i) => (
        <li key={i} className='ol-item label-value'>
          {largeList && largeList[i] && (
            <span className='label'>
              {largeList[i]}
              {comments && comments[i] && <span className='comment'>({comments[i]})</span>}
            </span>
          )}
          {smallList && smallList[i] && <span className='label-mobile'>{smallList[i]}</span>}
          {valueList && valueList[i] && <span className='value'>{valueList[i]}</span>}
        </li>
      ))}
    </ol>
  );
}

export default AcademicSection;
