import React from 'react';

function Banner({ list, links }) {
  return (
    <div className='table'>
      <div className='table-left'>
        <div className='photo'></div>
      </div>
      <div className='table-right'>
        <ul className='list-info'>
          <li className='list-header'>
            <h1>Phillip Stemmlar Schulze</h1>
          </li>
          {list.map((item, i) => (
            <li key={i} className='list-item'>
              {item}
            </li>
          ))}
          <li className='list-item icons icons-full'>
            {links.map((link, i) => {
              const [href, src, alt, colorClass] = link;
              return (
                <a key={i} href={href} target='_blank' rel='noopener noreferrer'>
                  <img alt={alt} src={src} className={'icon ' + (colorClass ? 'icon-black' : '')}></img>
                </a>
              );
            })}
          </li>
        </ul>
      </div>
    </div>
  );
}

export default Banner;
